import React  from 'react';
import axios from 'axios';
import './App.css';

class App extends React.Component {
    
  state = {
      advice: ''
  };

  componentDidMount(){
        this.fetchAdvice();

  }
  fetchAdvice = () =>{
      axios.get('https://api.adviceslip.com/advice')
      .then((Response)  => {
          const { advice }=Response.data.slip;

          this.setState({ advice });
      })
      .catch((error)=>{
          console.log(error);
      });
     
      //console.log('COMPONENT DID MOUNT'); checking 
  }

  render(){
    const { advice } = this.state;
  return(
      <div className="app">
          <div className="card">
            <h1 className="heading">
                {advice}
                </h1>
                <button className="button" onClick={this.fetchAdvice}>
                    <br></br>
                    <span>Give me advice</span>
                </button>
                <h5><a href="https://sureshturbo.github.io/profile/" target="_blank">Sureshrajan</a></h5>
  </div>
  
  
  </div>
  );
}
}
export default App;
